<template>
  <div class="data-api-container">
    <div class="artist-container">
      <div class="title-container">
        <h2>Nghệ sĩ nổi bật</h2>
      </div>
      <slider-artist-component />
    </div>
    <div class="music-container">
      <div class="title-container">
        <h2>Sản phẩm nổi bật</h2>
      </div>

      <div>
        <flickity class="flickity" ref="flickity" :options="flickityOptions">
          <div
            class="carousel-cell"
            v-for="value in data.music"
            :key="value.id"
          >
            <a
              class="item-music"
              :href="value.url"
              target="_blank"
              rel="noopener noreferrer"
            >
              <div class="carousel-img">
                <img :src="value.thumbnail" alt="" />
              </div>
              <div class="carousel-content">
                <h4>{{ value.name }}</h4>
                <p>{{ value.artist_info.name }}</p>
              </div>
            </a>
          </div>
        </flickity>
      </div>
    </div>
    <!-- <div class="blog-container">
      <div class="title-container">
        <h2>Các tin tức mới nhất</h2>
      </div>
      <div class="content-container">
        <el-row :gutter="20">
          <el-col :span="8" v-for="value in data.blog" :key="value.id">
            <div class="content-item">
              <div class="img-item">
                <img :src="value.thumbnail" alt="" />
              </div>
              <div class="data-item">
                <h4>{{ value.title }}</h4>
                <p>
                  {{ value.short_desription }}
                </p>
              </div>
            </div>
          </el-col>
        </el-row>
      </div>
    </div> -->
  </div>
</template>

<script>
import Flickity from "vue-flickity";
import axios from "axios";
import SliderArtistComponent from "./SliderArtistComponent.vue";
export default {
  components: {
    Flickity,
    SliderArtistComponent,
  },
  data() {
    return {
      flickityOptions: {
        initialIndex: 2,
        prevNextButtons: true,
        wrapAround: true,
        freeScroll: true,
        autoPlay: 1500,
        pauseAutoPlayOnHover: false,
        pageDots: false,
      },
      data: {
        blog: [],
        music: [],
        artist: [],
      },
    };
  },
  methods: {
    async getAllBlogHomePage() {
      await axios
        .get("blog/get-homepage")
        .then((result) => {
          this.data.blog = result.data.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    async getAllMusicHomePage() {
      await axios
        .get("artist/music/get-homepage")
        .then((result) => {
          this.data.music = result.data.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  async mounted() {
    await this.getAllBlogHomePage();
    await this.getAllMusicHomePage();
    await this.$refs.flickity.rerender();
  },
};
</script>

<style lang="scss">
.data-api-container {
  max-width: 1140px;
  margin: 0 auto;
}

.title-container {
  padding: 60px 0;
  text-align: center;
  h2 {
    display: inline-block;
    width: 70%;
    font-weight: 800;
    font-size: 36px;
    color: #b6ff52;
  }
}

.music-container {
  .carousel-cell {
    width: fit-content;
    height: fit-content;
    margin-right: 30px;
    border-radius: 5px;
    .item-music {
      border: 1px solid #ffff;
      display: block;
      width: fit-content;
      border-radius: 32px;
      .carousel-img {
        width: fit-content;
        height: fit-content;
        img {
          max-width: 255px;
          min-height: 291px;
          border-radius: 32px 32px 0px 0px;
        }
      }
      .carousel-content {
        min-height: 86px;
        border-radius: 0px 0px 32px 32px;
        background: linear-gradient(
          180deg,
          rgba(250, 250, 250, 0.08) 0%,
          rgba(250, 250, 250, 0.08) 100%
        );
        max-width: 255px;
        color: #ffff;
        padding: 12px 32px;
        h4 {
          color: #ffff;
          font-size: 16px;
          font-family: Manrope;
          font-weight: 600;
          line-height: 140%;
        }
        p {
          font-size: 14px;
          line-height: 140%;
          color: #ffff;
        }
      }
    }
  }
}

.content-container {
  .content-item {
    .img-item {
      img {
        max-width: 345px;
        min-height: 265px;
      }
    }

    .data-item {
      max-width: 345px;
      h4 {
        color: #fff;
        font-size: 24px;
        font-weight: 700;
        line-height: 120%;
      }
      p {
        color: var(--gray-5, #e0e0e0);
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 500px) {
  .data-api-container {
    .line {
      img {
        height: 1px;
      }
    }
  }
}
</style>
