<template>
  <div class="content-container">
    <el-carousel arrow="always" indicator-position="outside">
      <el-carousel-item
        v-for="value in data.artist"
        :key="value.id"
        class="overwrite-el-carousel__item"
      >
        <div class="item-carousel">
          <div class="container-carousel-item">
            <div class="content-item">
              <h3>{{ value.name }}</h3>
              <div class="job-list">
                <p
                  v-for="item in value.job"
                  :key="item.id"
                  class="job-item"
                  :style="{
                    background: item.background,
                  }"
                >
                  {{ item.name }}
                </p>
              </div>
              <div class="style-music-list">
                <p
                  class="style-music-item"
                  v-for="(item, index) in value.type"
                  :key="index"
                >
                  {{ item }}
                </p>
              </div>
              <div class="social-link">
                <a
                  v-for="item in value.social_url"
                  :key="item.social_url"
                  :href="item.social_url"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <img
                    v-if="item.type == 'facebook'"
                    src="@/assets/MiniIcon/bordered-icon-facebook.png"
                    :alt="item.type"
                  />
                  <img
                    v-else-if="item.type == 'yotube'"
                    src="@/assets/MiniIcon/bordered-icon-youtube.png"
                    :alt="item.type"
                  />
                  <img
                    v-else-if="item.type == 'instagram'"
                    src="@/assets/MiniIcon/bordered-icon-insta.png"
                    :alt="item.type"
                  />
                  <img
                    v-else-if="item.type == 'tiktok'"
                    src="@/assets/MiniIcon/bordered-icon-tiktok.png"
                    :alt="item.type"
                  />
                  <img
                    v-else-if="item.type == 'twitter'"
                    src="@/assets/MiniIcon/bordered-icon-twitter.png"
                    :alt="item.type"
                  />
                </a>
              </div>
            </div>

            <div class="img-item">
              <a
                :href="value.link_music"
                target="_blank"
                rel="noopener noreferrer"
              >
                <img src="./../../assets/DataAPIComponent/Group.png" alt="" />
              </a>
            </div>

            <div class="avatar-item">
              <img :src="value.avatar" alt="" />
            </div>
          </div>
        </div>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      data: {
        artist: [],
      },
    };
  },
  methods: {
    async getArtistHomePage() {
      await axios
        .get("artist/get-homepage")
        .then((result) => {
          this.data.artist = result.data.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  async mounted() {
    await this.getArtistHomePage();
  },
};
</script>

<style lang="scss">
.overwrite-el-carousel__item {
  background: linear-gradient(90deg, #6684be 0%, #aa8cf0 100%);
  border-radius: 32px;
  .item-carousel {
    width: 100%;
    .container-carousel-item {
      display: flex;
      margin: 0 auto;
      align-items: center;
      padding: 16px 160px 0px 160px;
      .content-item {
        flex: 1;
        h3 {
          font-weight: 700;
          line-height: 140%;
          color: var(--white, #fff);
          font-size: 30px;
        }

        .job-list {
          display: flex;
          align-items: flex-start;
          gap: 10px;
          flex-wrap: wrap;
          .job-item {
            border-radius: 100px;
            padding: 4px 16px;
            margin: unset;
          }
        }

        .style-music-list {
          display: flex;
          flex-wrap: wrap;
          padding-top: 10px;
          .style-music-item {
            display: inline-block;
            color: var(--white, #fff);
            font-size: 18px;
            font-family: Manrope;
            line-height: 160%;
            margin: unset;
            border: 1px solid var(--white, #fff);
            border-radius: 100px;
            padding: 4px 8px;
            margin: 0 12px 8px 0;
          }
        }

        .social-link {
          color: #fff;
          a {
            display: inline-block;
            margin: 0 16px;
            img {
              max-width: 40px;
              max-height: 40px;
            }
          }

          a:first-child {
            margin-left: unset;
          }
        }
      }
      .img-item {
        flex: 1;
        width: 100%;
        text-align: center;
        img {
          max-height: 85px;
          max-width: 85px;
        }
      }

      .avatar-item {
        flex: 1;
        width: 100%;
        text-align: right;
        img {
          max-height: 264px;
          max-width: 264px;
          border-radius: 20px;
        }
      }
    }
  }
}

.el-carousel__container {
  height: 300px;
}

.is-active {
  .el-carousel__button {
    background: #b6ff52;
  }
}

@media screen and (max-width: 500px) {
  .el-carousel__container {
    height: 265px;
  }

  .overwrite-el-carousel__item {
    background: linear-gradient(90deg, #6684be 0%, #aa8cf0 100%);
    border-radius: 32px;
    .item-carousel {
      width: 100%;
      .container-carousel-item {
        display: flex;
        flex-direction: row-reverse;
        padding: unset;
        .content-item {
          flex: 1;
          padding-left: 20px;
        }
        .img-item {
          display: none;
        }

        .avatar-item {
          flex: 1;
          width: 100%;
          text-align: right;
          img {
            max-height: 264px;
            max-width: 264px;
            border-radius: 20px;
          }
        }
      }
    }
  }
}
</style>
