<template>
  <div class="detail-container">
    <div class="landing-container">
      <div class="artist-landing">
        <div class="avatar">
          <img :src="data.avatar" :alt="data.name" />
        </div>
        <div class="content-item">
          <h3>{{ data.name }}</h3>
          <div class="job-list">
            <p
              v-for="item in data.job"
              :key="item.id"
              class="job-item"
              :style="{
                background: item.background,
              }"
            >
              {{ item.name }}
            </p>
          </div>
          <div class="style-music-list">
            <p
              class="style-music-item"
              v-for="(item, index) in data.type"
              :key="index"
            >
              {{ item }}
            </p>
          </div>
          <div class="social-link">
            <a
              v-for="item in data.social_url"
              :key="item.social_url"
              :href="item.social_url"
              target="_blank"
              rel="noopener noreferrer"
            >
              <img
                v-if="item.type == 'facebook'"
                src="@/assets/MiniIcon/bordered-icon-facebook.png"
                :alt="item.type"
              />
              <img
                v-else-if="item.type == 'yotube'"
                src="@/assets/MiniIcon/bordered-icon-youtube.png"
                :alt="item.type"
              />
              <img
                v-else-if="item.type == 'instagram'"
                src="@/assets/MiniIcon/bordered-icon-insta.png"
                :alt="item.type"
              />
              <img
                v-else-if="item.type == 'tiktok'"
                src="@/assets/MiniIcon/bordered-icon-tiktok.png"
                :alt="item.type"
              />
              <img
                v-else-if="item.type == 'twitter'"
                src="@/assets/MiniIcon/bordered-icon-twitter.png"
                :alt="item.type"
              />
            </a>
          </div>
        </div>
      </div>
      <div class="url-artist">
        <a
          :href="data.link_music"
          target="_blank"
          class="img-container-artist"
          rel="noopener noreferrer"
        >
          <img
            id="image-play"
            src="@/assets/DataAPIComponent/Group.png"
            alt=""
          />
        </a>
      </div>
    </div>

    <div class="info-artist">
      <h2 class="title-info">Tiểu sử {{ data.name }}</h2>

      <ul class="list-info">
        <li class="item-info">
          <div>
            <i class="fa-solid fa-user" style="color: #b6ff52"></i>
            Tên đầy đủ: <span>{{ data.full_name }}</span>
          </div>
        </li>
        <li class="item-info">
          <div>
            <i class="fa-solid fa-cake-candles" style="color: #b6ff52"></i>
            Ngày sinh: <span>{{ data.birth_day }} </span>
          </div>
        </li>
        <li class="item-info">
          <div>
            <i class="fa-solid fa-venus-mars" style="color: #b6ff52"></i>
            Giới tính: <span>{{ data.gender ? "Nam" : "Nữ" }} </span>
          </div>
        </li>
        <li class="item-info">
          <div>
            <i class="fa-solid fa-phone" style="color: #b6ff52"></i>
            Số điện thoại: <span>{{ data.phone }} </span>
          </div>
        </li>
        <li class="item-info" v-if="data.description">
          <div v-html="data.description"></div>
        </li>
      </ul>
    </div>

    <div class="list-song" v-if="data.music_info?.length">
      <h2 class="title-list-song">Danh sách bài hát</h2>
      <ul class="song-container">
        <li class="item-song" v-for="item in data.music_info" :key="item.id">
          <div class="thumbnail-song">
            <img :src="item.thumbnail" :alt="item.name" />
          </div>
          <div class="info-song">
            <p class="song-name">
              {{ item.name }}
            </p>

            <div class="song-type">
              <p v-for="(type, index) in item.type" :key="index">{{ type }}</p>
            </div>

            <p class="artist-name">
              {{ data.name }}
            </p>

            <div class="song-url">
              <a :href="item.url" target="_blank">
                <img src="@/assets/play.png" alt="Play" />
              </a>
            </div>
          </div>
        </li>
      </ul>
    </div>

    <div class="album-list" v-if="data.album_info?.length">
      <h2 class="title-list-album">Album</h2>
      <div class="row row-cols-2 row-cols-lg-4 g-2 g-lg-3">
        <div
          class="col"
          v-for="(album, index) in data.album_info.slice(0, 4)"
          :key="index"
        >
          <a
            class="item-album"
            :href="album.url"
            target="_blank"
            rel="noopener noreferrer"
          >
            <div class="carousel-img">
              <img :src="album.thumbnail" alt="" />
            </div>
            <div class="carousel-content">
              <h4>{{ album.name }}</h4>
              <p>{{ data.name }}</p>
            </div>
          </a>
        </div>
      </div>
    </div>

    <div class="key-word">
      <p>Từ khóa:&nbsp;</p>
      <h5>{{ data.name }}</h5>
    </div>

    <div class="line">
      <img src="@/assets/Divider.png" alt="" />
    </div>

    <div class="product-social social-network">
      <div class="title">
        <h5>Chia sẻ sản phẩm</h5>
      </div>
      <div class="social">
        <div class="social-flex">
          <div class="social-icon">
            <span class="col">
              <img
                src="@/assets/MiniIcon/bordered-icon-facebook.png"
                alt="facebook"
              />
            </span>
          </div>
          <!-- <div class="social-icon">
            <span class="col">
              <img
                src="@/assets/MiniIcon/bordered-icon-linkedin.png"
                alt="instagram"
              />
            </span>
          </div> -->
          <div class="social-icon">
            <span class="col">
              <img
                src="@/assets/MiniIcon/bordered-icon-zalo.png"
                alt="tiktok"
              />
            </span>
          </div>
          <div class="social-icon">
            <span class="col">
              <img
                src="@/assets/MiniIcon/bordered-icon-twitter.png"
                alt="twitter"
              />
            </span>
          </div>
        </div>
        <div class="share-count">{{ data.share }} lượt chia sẻ</div>
      </div>
    </div>

    <div class="line">
      <img src="@/assets/Divider.png" alt="" />
    </div>

    <div class="same-artist">
      <div class="title-same-artist">
        <h2>Nghệ sĩ tương tự</h2>

        <span> Xem tất cả &nbsp; > </span>
      </div>
      <div
        v-if="sameArtist.data?.length"
        class="row row-cols-2 row-cols-lg-4 g-2 g-lg-3"
      >
        <div
          class="col"
          v-for="value in sameArtist.data.slice(0, 4)"
          :key="value.id"
          @mouseenter="enterButton"
          @mouseleave="leaveButton"
          @click="pushToDetailPage(value.slug)"
        >
          <div class="card item-artist">
            <img
              :src="value.avatar"
              class="card-img-top avatar-artist"
              :alt="value.name"
            />
            <div class="card-body content-artist">
              <h5 class="card-title">
                {{ value.name }}
              </h5>
              <div class="job-list">
                <p
                  v-for="item in value.job"
                  :key="item.id"
                  class="job-item"
                  :style="{
                    background: item.background,
                  }"
                >
                  {{ item.name }}
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      data: [],
      sameArtist: [],
    };
  },
  methods: {
    getArtistBySlug() {
      axios
        .get(`artist/slug?slug=${this.$route.params.slug}`)
        .then((result) => {
          this.data = result.data.data.artist;
          this.sameArtist = result.data.data.same_data;
        })
        .catch((err) => {
          console.log(err);
        });
    },

    animateButton(el, scale, duration, elasticity) {
      this.$anime.remove(el);
      this.$anime({
        targets: el,
        scale: scale,
        duration: duration,
        elasticity: elasticity,
      });
    },

    enterButton(el) {
      this.animateButton(el.target, 1.1, 800, 400);
    },

    leaveButton(el) {
      this.animateButton(el.target, 1.0, 600, 300);
    },

    async pushToDetailPage(value) {
      await this.$router.push(`/nghe-si/${value}`).catch(() => {});
      await this.getArtistBySlug();
    },
  },
  async mounted() {
    await this.getArtistBySlug();
    this.$anime({
      targets: "#image-play",
      rotate: {
        value: 360,
        duration: 3000,
      },
      easing: "linear",
      elasticity: 0,
      loop: true,
    });
  },
};
</script>

<style lang="scss" scoped>
.detail-container {
  max-width: 1140px;
  margin: 0 auto;
  .landing-container {
    width: 100%;
    display: flex;
    align-items: center;
    padding: 60px 0;
    .artist-landing {
      flex: 1;
      display: flex;
      justify-content: space-between;
      .avatar {
        max-width: 240px;
        max-height: 240px;
        img {
          border-radius: 50%;
          width: 100%;
          height: 100%;
          object-fit: fill;
        }
      }
      .content-item {
        padding: 20px 0;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        h3 {
          font-weight: 700;
          line-height: 140%;
          color: var(--white, #fff);
          font-size: 30px;
        }

        .job-list {
          display: flex;
          align-items: flex-start;
          gap: 10px;
          flex-wrap: wrap;
          color: #fff;
          .job-item {
            border-radius: 100px;
            padding: 4px 16px;
            margin: unset;
          }
        }

        .style-music-list {
          display: flex;
          flex-wrap: wrap;
          padding-top: 10px;
          .style-music-item {
            display: inline-block;
            color: var(--white, #fff);
            font-size: 18px;
            font-family: Manrope;
            line-height: 160%;
            margin: unset;
            border: 1px solid var(--white, #fff);
            border-radius: 100px;
            padding: 4px 8px;
            margin: 0 12px 8px 0;
          }
        }

        .social-link {
          color: #fff;
          a {
            display: inline-block;
            margin: 0 16px;
            img {
              max-width: 40px;
              max-height: 40px;
            }
          }

          a:first-child {
            margin-left: unset;
          }
        }
      }
    }
    .url-artist {
      flex: 1;
      .img-container-artist {
        max-width: 112px;
        max-height: 112px;
        display: block;
        padding: 0;
        margin: 0 auto;
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
  }

  .info-artist {
    padding: 60px;
    background: linear-gradient(
      92.98deg,
      rgba(255, 255, 255, 0.042) 0%,
      rgba(255, 255, 255, 0.024) 100%
    );
    border-radius: 32px;
    color: #ffff;
    .title-info {
      font-size: 32px;
      font-weight: 800;
      color: #b6ff52;
      padding: 0 0 32px 0;
    }
    .list-info {
      .item-info {
        div {
          padding: 0 0 20px 0;
          i {
            padding-right: 8px;
          }
          span {
            font-weight: 700;
          }
        }
      }
      .item-info:last-child {
        div {
          padding: unset;
        }
      }
    }
  }

  .list-song {
    padding: 80px 0;
    .title-list-song {
      font-size: 32px;
      font-weight: 800;
      color: #b6ff52;
      padding-bottom: 24px;
    }

    .song-container {
      display: flex;
      flex-direction: column;
      color: #ffff;
      .item-song {
        display: flex;
        padding: 12px;
        background: linear-gradient(
          92.98deg,
          rgba(255, 255, 255, 0.042) 0%,
          rgba(255, 255, 255, 0.024) 100%
        );
        border-radius: 16px;
        .thumbnail-song {
          max-width: 80px;
          max-height: 80px;
          img {
            width: 100%;
            object-fit: fill;
          }
        }
        .info-song {
          padding: 0 60px 0 20px;
          flex: 1;
          display: flex;
          justify-content: space-between;
          align-items: center;
          p {
            margin: 0;
            padding: 0;
            display: inline-block;
          }
          .song-type {
            p::after {
              content: ",";
              padding: 0 4px 0 0;
            }

            p:last-child::after {
              content: unset;
            }
          }

          .song-url {
            max-width: 40px;
            max-height: 40px;
            img {
              width: 100%;
              object-fit: fill;
            }
          }
        }
      }
    }
  }

  .album-list {
    .title-list-album {
      font-size: 32px;
      font-weight: 800;
      color: #b6ff52;
      padding-bottom: 24px;
    }

    .item-album {
      width: fit-content;
      display: block;
      border: 1px solid #ffffff;
      border-radius: 32px;
      .carousel-img {
        width: fit-content;
        height: fit-content;
        img {
          max-width: 255px;
          min-height: 291px;
          border-radius: 32px 32px 0px 0px;
        }
      }
      .carousel-content {
        min-height: 86px;
        border-radius: 0px 0px 32px 32px;
        background: linear-gradient(
          180deg,
          rgba(250, 250, 250, 0.08) 0%,
          rgba(250, 250, 250, 0.08) 100%
        );
        max-width: 255px;
        color: #ffff;

        padding: 12px 32px;
        h4 {
          color: #ffff;
          font-size: 16px;
          font-family: Manrope;
          font-weight: 600;
          line-height: 140%;
        }
        p {
          font-size: 14px;
          line-height: 140%;
          color: #ffff;
        }
      }
    }
  }

  .key-word {
    display: inline-block;
    font-size: 16px;
    font-weight: 700;
    padding: 36px 0;

    p {
      display: inherit;
      font: inherit;
      color: #ffff;
    }
    h5 {
      display: inherit;
      font: inherit;
      color: #b6ff52;
    }

    h5:hover {
      text-decoration: underline;
    }
  }

  .product-social {
    padding: 0 0 30px 0;
    display: flex;
    justify-content: space-between;
    color: #ffff;
    .title h5 {
      font-size: 18px;
      font-weight: 700;
    }
    .social {
      align-self: end;
      .social-flex {
        display: flex;
        .social-icon {
          padding: 10px 10px 10px 0;
          span img {
            max-width: 36px;
          }
        }
        .social-icon:last-child {
          padding-right: unset;
        }
      }
      .share-count {
        text-align: right;
      }
    }
  }

  .same-artist {
    .title-same-artist {
      color: #b6ff52;
      display: flex;
      justify-content: space-between;
      align-content: center;
      padding-bottom: 36px;
      h2 {
        font-weight: 800;
        font-size: 32px;
      }
      span {
        font-weight: 700;
        font-size: 16px;
        display: block;
        align-self: center;
        cursor: pointer;
      }
    }
  }
}

.line {
  width: 100%;
  img {
    width: 100%;
  }
}
</style>
